<template>
  <div class="home">
    <div class="shop-list">
      <div
        class="shop-box"
        :class="{active: item.isShow}"
        v-for="(item,index) in pro"
        :key="index"
        v-on:click.stop="show(item)"
      >
        <div class="check-box" v-on:click.stop="isGou(item)">
          <van-checkbox v-model="item.checked"></van-checkbox>
        </div>
        <div class="shop">
          <div class="img-box">
            <img :src="item.url" />
          </div>
          <div class="dec">
            <div class="shop-name">
              <span>{{item.name}}</span>
            </div>
            <div class="price-box" v-show="!item.isShow">
              <span>单价:￥{{item.price}}</span>
              <span>数量；{{item.num}}</span>
            </div>
            <div class="add" v-show="item.isShow">
              <button v-on:click.stop="jian(item)">-</button>
              <input type="text" v-model="item.num" />
              <button v-on:click.stop="add(item)">+</button>
            </div>
          </div>

          <div class="del" v-show="item.isShow">
            <van-button type="warning" v-on:click.stop="del(item,index)">删除</van-button>
          </div>
        </div>
      </div>
    </div>

    <van-submit-bar :price="allPrice" button-text="提交订单">
      <van-checkbox class="allselect" v-model="checked" @click="allgou">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>
<style lang="less" scoped>
.shop-list {
  width: 100%;

  .shop-box {
    margin: 0.133rem;
    border: 1px solid #eee;
    height: 2.667rem;
    overflow: hidden;

    .check-box {
      float: left;
      margin-left: 0.2rem;
      margin-top: 1rem;
    }

    .shop {
      float: left;
      overflow: hidden;

      .img-box {
        float: left;
        width: 2.667rem;
        > img {
          display: block;
          width: 100%;
        }
      }
      .dec {
        float: left;
        .shop-name {
          font-size: 0.45rem;
          margin: 0.1rem 0;
        }
        .price-box {
          margin-top: 0.8rem;
          font-size: 0.2rem;
          > span {
            margin-right: 0.5rem;
          }
        }
        .add {
          input {
            margin-top: 0.8rem;

            text-align: center;
            width: 1rem;
          }
        }
      }
      .del {
        float: left;
        margin-left: 0.5rem;
        margin-top: 0.7rem;
      }
    }
  }
  .active {
    border-color: red;
  }
}

.allselect {
  margin-left: 0.2rem;
}
</style>

<script>
export default {
  name: "home",

  data() {
    return {
      pro: [
        {
          name: "荣耀9X 麒麟810",
          num: 2,
          price: 200,
          url:
            "https://res.vmallres.com/pimages//product/6901443321339/78_78_1563503515663mp.png",
          checked: false,
          isShow: false,
          isActive: 1
        },
        {
          name: "荣耀9X 麒麟810",
          num: 2,
          price: 500,
          url:
            "https://res.vmallres.com/pimages//product/6901443321339/78_78_1563503515663mp.png",
          checked: false,
          isShow: false,
          isActive: 1
        },
        {
          name: "荣耀9X 麒麟810",
          num: 2,
          price: 100,
          url:
            "https://res.vmallres.com/pimages//product/6901443321339/78_78_1563503515663mp.png",
          checked: false,
          isShow: false,
          isActive: 1
        },
        {
          name: "荣耀9X 麒麟810",
          num: 2,
          price: 999,
          url:
            "https://res.vmallres.com/pimages//product/6901443321339/78_78_1563503515663mp.png",
          checked: false,
          isShow: false,
          isActive: 1
        }
      ],

      checked: false,
      allselectSign: 1,

      allPrice: 0,

      proData:null
    };
  },

  methods: {
  
    show(i) {
      for (let j = 0; j < this.pro.length; j++) {
        this.pro[j].isShow = false;
      }
      if (i.isActive == 1) {
      i.isShow = !i.isShow;
      i.isActive = 0;
      } else {
      i.isActive = 1;
      }
    },
    //加号
    add(item) {
      let _price = null;
      item.num++;
      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == true) {
          _price += this.pro[i].price * this.pro[i].num;
        }
      }
      this.allPrice = _price ? _price * 100 : 0;
    },
    //减号
    jian(item) {
      let _price = null;

      item.num--;
      item.num = item.num < 1 ? 1 : item.num;
      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == true) {
          _price += this.pro[i].price * this.pro[i].num;
        }
      }
      this.allPrice = _price ? _price * 100 : 0;
    },

    //遍历是否全部都勾选
    isGou(item) {
      let _price = null;
      item.checked = !item.checked;
      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == false) {
          this.checked = false;
          this.allselectSign = 1;
          break;
        } else {
          this.checked = true;
          this.allselectSign = 0;
        }
      }

      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == true) {
          _price += this.pro[i].price * this.pro[i].num;
        }
      }
      this.allPrice = _price ? _price * 100 : 0;

      
    },

    //全选
    allgou() {
      let _price = null;
      if (this.allselectSign == 1) {
        for (let i = 0; i < this.pro.length; i++) {
          this.pro[i].checked = true;
        }
        this.allselectSign = 0;
      } else {
        for (let i = 0; i < this.pro.length; i++) {
          this.pro[i].checked = false;
        }
        this.allselectSign = 1;
      }

      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == true) {
          _price += this.pro[i].price * this.pro[i].num;
        }
      }
      this.allPrice = _price ? _price * 100 : 0;
    },

    //删除
    del(item, i) {
      this.pro.splice(i, 1);
      let _price = null;
      item.checked = !item.checked;
      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == false) {
          this.checked = false;
          this.allselectSign = 1;
          break;
        } else {
          this.checked = true;
          this.allselectSign = 0;
        }
      }

      for (let i = 0; i < this.pro.length; i++) {
        if (this.pro[i].checked == true) {
          _price += this.pro[i].price * this.pro[i].num;
        }
      }
      this.allPrice = _price ? _price * 100 : 0;
    },


    http() {
      return new Promise((resolve, reject) => {
        let xhr = new XMLHttpRequest();

        xhr.onreadystatechange = function() {
          if (this.readyState === 4 && this.status === 200) {
            resolve(this.response);
          }
        };
        xhr.open("GET", "http://api.kangliuyong.com:9002/pros?count=5", true);

        //设置服务器响应的数据类型
        xhr.responseType = "json";

        xhr.send(null);
      });
    }
  },

  created(){
    let self =this;

      this.http().then(result => {
        self.proData = result;
        for (let i = 0; i < self.proData.length; i++) {
          
          self.proData[i].checked=false
        self.proData[i].isShow=false
        self.proData[i].isActive=1
        self.proData[i].num=1
        }
        

      })
},


};
</script>
